import React, { Component } from 'react';
import { AreaChart, Area } from 'recharts';

class StockTrendChart extends Component {
  render() {
    const data = this.props.history.map(item => ({ uv: item[0] || 0 }));
    return (
      <AreaChart
        width={130}
        height={40}
        data={data}
        margin={{
          top: 3, right: 0, left: 0, bottom: 3,
        }}>
        <Area type="monotone" dataKey="uv" stroke="#8884d8" fill={this.props.color} />
      </AreaChart>
    );
  }
}

export default StockTrendChart;
